@import "../../Assets/Styles/variables.scss";

.hide {
  display: none;
}

.course-card {
  width: 300px;
  overflow: hidden;
  border-radius: 8px;
  display: flex;
  flex-direction: column;

  &:hover {
    .shadow {
      background-color: rgba(0, 0, 0, 0.464);
    }
  }

  h1 {
    margin: 0;
    font-size: 1rem;
    text-transform: capitalize;
    color: $header-color;
  }

  .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background-color: transparent;
    transition: background-color 500ms;
  }

  .image {
    height: 200px;
    overflow: hidden;
    border-radius: 8px;
    position: relative;
  }

  .tags {
    display: flex;
    justify-content: space-between;
    padding: 0.7rem 1rem;
    text-transform: uppercase;

    span {
      z-index: 2;
      font-size: 0.7rem;
      padding: 0.5rem 1rem;
      border-radius: 1rem;

      &:nth-of-type(1) {
        color: white;
      }
    }
  }

  .new {
    background: $bg-quaternary-color;
  }

  .popular {
    background: $primary-color;
  }

  .best-seller {
    color: $header-color;
    background: $bg-six-color;
  }

  .ranking {
    padding: 1rem 0;

    & > * {
      padding: 0 0.125rem;
    }

    span {
      &:first-of-type {
        color: #e59819;
      }
      &:last-of-type {
        font-size: 0.75rem;
        color: $text-color;
      }
    }
  }

  .author-pricing {
    margin-top: 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .author {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      color: $text-color;
      font-size: 0.95rem;

      img {
        border-radius: 1rem;
      }
    }

    .price {
      .old {
        font-size: 0.95rem;
        color: $text-color;
        text-decoration: line-through;
      }

      .value {
        color: $header-color;
        margin-left: 0.5rem;
        font-weight: bold;
      }
    }
  }

  .misc {
    display: flex;
    justify-content: space-between;
    font-size: 0.8rem;
    padding: 1rem 0 0.5rem;
    color: $text-color;
    border-bottom: 1px solid $border-primary-color;

    div {
      display: flex;
      align-items: center;
      gap: 0.5rem;
    }
  }
}
