.slide_container {
    background-image: url("../../../Assets/Images/Frame.svg");
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: right bottom;
    padding: 10vw 0;
    
    .text {
        width: 30%;
        color: $header-color;
        text-transform: capitalize;
        transform: translateX(50%);

        h1 {
            display: flex;
            flex-direction: column;
            font-size: 3vw;
            font-weight: bolder;
            margin: 0;

            .career {
                color: $primary-color;
                text-decoration: underline;
            }
        }

        p {
            display: flex;
            flex-direction: column;
            padding: 0.25rem 0;
        }

        .buttons {
            display: flex;
            gap: 1rem;

            button {
                padding: 0.7rem 2.2rem;
                border-radius: 2rem;
                text-transform: capitalize;
                
                &:nth-of-type(1) {
                    color: white;
                    border: none;
                    background-color: $primary-color;
                }

                &:nth-of-type(2) {
                    color: $secondary-color;
                    border: 1px solid $secondary-color;
                    background: transparent;
                }
            }
        }
    }
}

.logos {
    display: flex;
    padding: 1.5rem 7rem;
    justify-content: space-evenly;
    background-color: $bg-tertiary-color;

    img {
        width: 3rem;
    }
}